.block1{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .title {
      color: white;
      text-align: center;
      margin-bottom: 1rem;
    }
    
    .subtitle {
      color: rgb(218, 218, 218);
      text-align: center;
      margin-bottom: 2rem;
    }
    
    .inputs_container {
      display: flex;
      flex-direction: column;
    }
    
    .inputs_container input {
      width: 85%;
      margin: 1rem auto;
      padding: 0.5rem;
      border-radius: 25px;
      font-size: 1rem;
      font-family: inherit;
      outline: 0;
      border: 0;
    }
    
    .login_button {
      display: block;
      margin: 2rem auto 1rem auto;
      width: 55%;
      padding: 0.5rem 0.8rem;
      border-radius: 25px;
      outline: 0;
      font-size: 1rem;
      font-family: inherit;
      cursor: pointer;
      background-color: #4e65f7;
      color: white;
      border: 2px solid transparent;
    }
    
    .login_button:hover {
      border: 2px solid #4e65f7;
      background-color: #000;
    }
    
    .link_container {
      display: flex;
      justify-content: center;
    }
    
    .small {
      color: rgb(218, 218, 218);
      text-align: center;
      font-size: 0.85rem;
      text-decoration: underline;
      margin-bottom: 2rem;
      cursor: pointer;
    }
    
    .icons {
      display: flex;
      justify-content: center;
      gap: 2rem;
    }
    
    .icon {
      color: white;
      cursor: pointer;
    }
    
    .error_msg {
      color: red;
      padding-left: 2.5rem;
      font-size: 0.85rem;
      margin: 0;
    }
    
    @media only screen and (max-width: 620px) {
      .inputs_container input {
        width: 100%;
      }
    
      .login_button {
        width: 100%;
      }
    
      .error_msg {
        padding-left: 0.2rem;
      }
    }
    
    @media only screen and (max-height: 670px) {
      .subtitle {
        margin-bottom: 2rem;
      }
    
      .small {
        margin-bottom: 3rem;
      }
    }